import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import Navbar from '../Navbar';
import Sidebar from '../../../admin/sidebar';

const Showproduct = () => {
  const navigate=useNavigate();
  const [products, setProducts] = useState([]);

  useEffect(() => {
    // Fetch the list of products from the server
    axios
      .get('http://localhost:8000/api/show-product')
      .then((response) => {
        setProducts(response.data.Product); // Assuming "Product" is the key containing the array
      })
      .catch((error) => {
        console.error('Error fetching products:', error);
      });
  }, []);



  // ...
  
  const deleteProduct = (id) => {
    axios
      .delete(`http://localhost:8000/api/delete-product/${id}`)
      .then((response) => {
        // Handle success, e.g., remove the deleted product from the UI
        console.log('Product deleted successfully.');
      })
      .catch((error) => {
        // Handle error, e.g., show an error message to the user
        console.error('Error deleting product:', error);
      });
  };
  
  // ...
  

  

  return (
    <div className=''>
        <div className='sb-nav-fixed'>
         <Navbar/>
      <div id='layoutSidenav'>
        <div id="layoutSidenav_nav">
     <Sidebar/>
        </div>
     
        <div class="lg:w-full lg:ml-64 px-6 py-8">
      <h2 className='font-semibold justify-center items-center'>Product List</h2>

      <div className="flex flex-col">
        <div className="overflow-x-auto sm:-mx-6 lg:-mx-8">
          <div className="inline-block min-w-full py-2 sm:px-6 lg:px-8">
            <div className="overflow-hidden">
              <table className="min-w-full text-left text-sm font-light">
                <thead className="border-b bg-white font-medium dark:border-neutral-500 dark:bg-neutral-600">
                  <tr>
                    <th scope="col" className="px-6 py-4">#</th>
                    <th scope="col" className="px-6 py-4">Title</th>
                    <th scope="col" className="px-6 py-4">Description</th>
                    <th scope="col" className="px-6 py-4">Image</th>
                    <th scope="col" className="px-6 py-4">action</th>
                  </tr>
                </thead>
                <tbody>
                  {products.map((product) => (
                    <tr
                      key={product.id}
                      className="border-b bg-neutral-100 dark:border-neutral-500 dark:bg-neutral-700"
                    >
                      <td className="whitespace-nowrap px-6 py-4 font-medium">{product.id}</td>
                      <td className="whitespace-nowrap px-6 py-4">{product.title}</td>
                      <td className="whitespace-nowrap px-6 py-4 flex-wrap">{product.description}</td>
                      <td className="whitespace-nowrap px-6 py-4">
                        <img
                          src={`http://localhost:8000/uploads/${product.image}`}
                          alt={product.title}
                          width="100" // Set the width as needed
                          height="100" // Set the height as needed
                        />
                      </td>
                      <Link to={`/product-detailes/${product.id}`}  className='text-white mt-10  text-xl font-semibold bg-blue-400 rounded-3xl w-20 hover:bg-blue-950 mx-3'>View</Link>

                      <button type='submit' onClick={() => deleteProduct(product.id)} className='text-white mt-10 text-xl font-semibold bg-red-400 rounded-3xl w-20 hover:bg-red-950'>Delete</button>
                      <Link to={`/edit-product/${product.id}`}  className='text-white mt-10  text-xl font-semibold bg-blue-400 rounded-3xl w-20 hover:bg-blue-950 mx-3'>Edit</Link>
                    </tr>
                   
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
    </div>
    </div>
  );
};

export default Showproduct;
